import React from 'react';

import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import PanelHeaderBack from "@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack";
import Group from "@vkontakte/vkui/dist/components/Group/Group";
import List from "@vkontakte/vkui/dist/components/List/List";
import Snackbar from "@vkontakte/vkui/dist/components/Snackbar/Snackbar";
import SimpleCell from "@vkontakte/vkui/dist/components/SimpleCell/SimpleCell";
import Icon28UsersOutline from '@vkontakte/icons/dist/28/users_outline';
import Icon28PhoneOutline from '@vkontakte/icons/dist/28/phone_outline';
import Icon28MailOutline from '@vkontakte/icons/dist/28/mail_outline';
import Icon28PlaceOutline from '@vkontakte/icons/dist/28/place_outline';

class ContactsPanel extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      snackbar: null,
    };
  }

  render() {
    return (
      <Panel id="ContactsPanel">
        <PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>Контакты</PanelHeader>
        
        <Group>
          <List>
            <SimpleCell before={<Icon28UsersOutline />} onClick={() => document.getElementById("communityLink").click()}>Сообщество PARADISE SUN</SimpleCell>
            <SimpleCell before={<Icon28PhoneOutline />} onClick={() => document.getElementById("telLink").click()}>+7 917 802-13-33</SimpleCell>
            <SimpleCell before={<Icon28MailOutline />} onClick={() => document.getElementById("mailLink").click()}>paradise.sun@mail.ru</SimpleCell>
            <SimpleCell 
              before={<Icon28PlaceOutline />} 
              onClick={() => {
                navigator.clipboard.writeText("г. Салават, ул. Калинина д. 82A");
                this.setState(() => ({snackbar: <Snackbar onClose={() => {}}>Скопировано!</Snackbar>}))
              }}>
              г. Салават, ул. Калинина д. 82A</SimpleCell>

            <a id="telLink" hidden href="tel: +79178021333"></a>
            <a id="mailLink" hidden href="mailto: paradise.sun@mail.ru"></a>
            <a id="communityLink" hidden href="https://vk.com/club27450268"></a>
          </List>
        </Group>

        {this.state.snackbar}
      </Panel>
    );
  }
}

export default ContactsPanel;
